let fullpage = null || fullpage;
let fp = new fullpage("#fullpage", {
    licenseKey: "OPEN-SOURCE-GPLV3-LICENSE",
});

document.querySelector("#foru").addEventListener("click", function(event){
    event.preventDefault();
    fp.moveTo(2);
}.bind(this));

